import React from "react";
import "./SideBar.sass";
import { Link } from "gatsby";

export class SideBarQuickstart extends React.Component<{}>
{
  render()
  {
    return (
      <div className="sidebar-component">
        <div className="sidebar-header">
            Quickstart
        </div>
        <div className="sidebar-content">
          <ul>
            <li>
              <Link to="/was-ist-power-bi">Was ist Power BI?</Link>
            </li>
          </ul>
        </div>
      </div>
    )
  }
}